'use client';

import { useState, useEffect } from 'react';
import { AppLayout } from '@/components/layout/AppLayout';
import { Button } from '@/components/ui/Button';
import { Card } from '@/components/ui/Card';
import { Alert } from '@/components/ui/Alert';
import { useAuth } from '@/components/auth/SessionManager';
import { 
  TrendingUp, 
  TrendingDown, 
  BarChart3, 
  Calendar, 
  Target, 
  Award, 
  Clock,
  Star,
  ArrowUp,
  ArrowDown,
  Minus,
  Filter,
  Download,
  Share2,
  CheckCircle,
  Play
} from 'lucide-react';

interface ProgressData {
  overallTrend: {
    currentScore: number;
    previousScore: number;
    change: number;
    trend: 'up' | 'down' | 'stable';
  };
  categoryTrends: {
    [key: string]: {
      current: number;
      previous: number;
      change: number;
      trend: 'up' | 'down' | 'stable';
    };
  };
  sessionHistory: SessionData[];
  achievements: Achievement[];
  recommendations: string[];
  benchmarks: {
    userAverage: number;
    globalAverage: number;
    roleAverage: number;
  };
}

interface SessionData {
  id: string;
  date: Date;
  role: string;
  company: string;
  type: 'behavioral' | 'technical' | 'mixed';
  overallScore: number;
  scores: {
    confidence: number;
    clarity: number;
    relevance: number;
    structure: number;
  };
  duration: number;
}

interface Achievement {
  id: string;
  title: string;
  description: string;
  icon: string;
  unlockedAt: Date;
  category: 'score' | 'consistency' | 'improvement' | 'milestone';
}

// Empty progress data for new users
const EMPTY_PROGRESS_DATA: ProgressData = {
  overallTrend: {
    currentScore: 0,
    previousScore: 0,
    change: 0,
    trend: 'stable'
  },
  categoryTrends: {
    confidence: { current: 0, previous: 0, change: 0, trend: 'stable' },
    clarity: { current: 0, previous: 0, change: 0, trend: 'stable' },
    relevance: { current: 0, previous: 0, change: 0, trend: 'stable' },
    structure: { current: 0, previous: 0, change: 0, trend: 'stable' }
  },
  sessionHistory: [],
  achievements: [],
  recommendations: [],
  benchmarks: {
    userAverage: 0,
    globalAverage: 0,
    roleAverage: 0
  }
};

export default function ProgressPage() {
  const { user, isAuthenticated } = useAuth();
  const [progressData, setProgressData] = useState<ProgressData | null>(null);
  const [loading, setLoading] = useState(true);
  const [timeRange, setTimeRange] = useState<'week' | 'month' | 'quarter' | 'year'>('month');
  const [activeTab, setActiveTab] = useState<'overview' | 'trends' | 'achievements' | 'benchmarks'>('overview');

  useEffect(() => {
    const loadProgressData = async () => {
      setLoading(true);
      try {
        // Simulate API call
        await new Promise(resolve => setTimeout(resolve, 1000));
        setProgressData(EMPTY_PROGRESS_DATA); // Empty data for new users
      } catch (error) {
        console.error('Failed to load progress data:', error);
      } finally {
        setLoading(false);
      }
    };

    if (isAuthenticated) {
      loadProgressData();
    }
  }, [isAuthenticated, timeRange]);

  const getTrendIcon = (trend: 'up' | 'down' | 'stable') => {
    switch (trend) {
      case 'up': return <ArrowUp className="w-4 h-4 text-green-500" />;
      case 'down': return <ArrowDown className="w-4 h-4 text-red-500" />;
      case 'stable': return <Minus className="w-4 h-4 text-gray-500" />;
    }
  };

  const getTrendColor = (trend: 'up' | 'down' | 'stable') => {
    switch (trend) {
      case 'up': return 'text-green-600';
      case 'down': return 'text-red-600';
      case 'stable': return 'text-gray-600';
    }
  };

  const getScoreColor = (score: number): string => {
    if (score >= 80) return 'text-green-600';
    if (score >= 60) return 'text-yellow-600';
    return 'text-red-600';
  };

  const getCategoryIcon = (category: string) => {
    switch (category) {
      case 'milestone': return 'üèÜ';
      case 'score': return '‚≠ê';
      case 'consistency': return 'üî•';
      case 'improvement': return 'üìà';
      default: return 'üéØ';
    }
  };

  const handleExportProgress = () => {
    if (!progressData) return;
    
    const exportData = {
      progressData,
      exportedAt: new Date().toISOString(),
      exportedBy: user?.email,
      timeRange
    };
    
    const blob = new Blob([JSON.stringify(exportData, null, 2)], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `progress-report-${new Date().toISOString().split('T')[0]}.json`;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  };

  const handleShareProgress = () => {
    if (navigator.share && progressData) {
      navigator.share({
        title: 'My Interview Progress',
        text: `I've been improving my interview skills! Current average score: ${progressData.overallTrend.currentScore}%`,
        url: window.location.href
      });
    } else if (progressData) {
      navigator.clipboard.writeText(
        `I've been improving my interview skills! Current average score: ${progressData.overallTrend.currentScore}%`
      );
    }
  };

  if (!isAuthenticated) {
    return (
      <AppLayout>
        <div className="flex items-center justify-center min-h-screen">
          <Alert variant="warning" title="Authentication Required">
            Please sign in to access your progress tracking.
          </Alert>
        </div>
      </AppLayout>
    );
  }

  if (loading) {
    return (
      <AppLayout>
        <div className="flex items-center justify-center min-h-screen">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
          <span className="ml-3 text-gray-600">Loading progress data...</span>
        </div>
      </AppLayout>
    );
  }

  if (!progressData) {
    return (
      <AppLayout>
        <div className="flex items-center justify-center min-h-screen">
          <Alert variant="error" title="No Progress Data">
            Complete some interviews to see your progress tracking.
          </Alert>
        </div>
      </AppLayout>
    );
  }

  return (
    <AppLayout>
      <div className="min-h-screen bg-gray-50 py-8">
        <div className="max-w-7xl mx-auto px-6">
          {/* Header */}
          <div className="mb-8">
            <div className="flex items-center justify-between">
              <div>
                <h1 className="text-3xl font-bold text-gray-900">Progress Tracking</h1>
                <p className="text-gray-900 mt-2">
                  Monitor your interview performance and track improvement over time
                </p>
              </div>
              
              <div className="flex items-center space-x-4">
                {/* Time Range Filter */}
                <select
                  value={timeRange}
                  onChange={(e) => setTimeRange(e.target.value as any)}
                  className="px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                >
                  <option value="week">Last Week</option>
                  <option value="month">Last Month</option>
                  <option value="quarter">Last Quarter</option>
                  <option value="year">Last Year</option>
                </select>
                
                <Button
                  variant="secondary"
                  onClick={handleExportProgress}
                  className="flex items-center"
                >
                  <Download className="w-4 h-4 mr-2" />
                  Export
                </Button>
                
                <Button
                  variant="secondary"
                  onClick={handleShareProgress}
                  className="flex items-center"
                >
                  <Share2 className="w-4 h-4 mr-2" />
                  Share
                </Button>
              </div>
            </div>
          </div>

          {/* Overall Progress Summary */}
          <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-8">
            <Card className="p-6 text-center">
              <div className={`text-4xl font-bold mb-2 text-gray-400`}>
                0%
              </div>
              <div className="text-sm text-gray-900 mb-2">Current Average</div>
              <div className="text-sm text-gray-900">
                Start practicing to see progress
              </div>
            </Card>

            <Card className="p-6 text-center">
              <div className="text-4xl font-bold text-gray-900 mb-2">
                0
              </div>
              <div className="text-sm text-gray-900 mb-2">Total Sessions</div>
              <div className="text-sm text-gray-900">
                0h practiced
              </div>
            </Card>

            <Card className="p-6 text-center">
              <div className="text-4xl font-bold text-gray-900 mb-2">
                0
              </div>
              <div className="text-sm text-gray-900 mb-2">Achievements</div>
              <div className="text-sm text-gray-900">
                Complete interviews to unlock
              </div>
            </Card>

            <Card className="p-6 text-center">
              <div className="text-4xl font-bold text-blue-600 mb-2">
                -
              </div>
              <div className="text-sm text-gray-900 mb-2">Global Rank</div>
              <div className="text-sm text-gray-900">
                Start practicing to get ranked
              </div>
            </Card>
          </div>

          {/* Tabs */}
          <div className="border-b border-gray-200 mb-8">
            <nav className="-mb-px flex space-x-8">
              {[
                { id: 'overview', label: 'Overview', icon: BarChart3 },
                { id: 'trends', label: 'Trends', icon: TrendingUp },
                { id: 'achievements', label: 'Achievements', icon: Award },
                { id: 'benchmarks', label: 'Benchmarks', icon: Target }
              ].map(tab => (
                <button
                  key={tab.id}
                  onClick={() => setActiveTab(tab.id as any)}
                  className={`flex items-center py-2 px-1 border-b-2 font-medium text-sm ${
                    activeTab === tab.id
                      ? 'border-blue-500 text-blue-600'
                      : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                  }`}
                >
                  <tab.icon className="w-4 h-4 mr-2" />
                  {tab.label}
                </button>
              ))}
            </nav>
          </div>

          {/* Tab Content */}
          {activeTab === 'overview' && (
            <div className="text-center py-12">
              <BarChart3 className="h-16 w-16 text-gray-400 mx-auto mb-6" />
              <h3 className="text-xl font-medium text-gray-900 mb-4">No Progress Data Yet</h3>
              <p className="text-gray-900 mb-8 max-w-md mx-auto">
                Complete your first interview to start tracking your performance and see detailed analytics here.
              </p>
              <Button 
                onClick={() => window.location.href = '/interview'}
                className="flex items-center mx-auto"
              >
                <Play className="mr-2 h-4 w-4" />
                Start Your First Interview
              </Button>
            </div>
          )}

          {activeTab === 'trends' && (
            <div className="text-center py-12">
              <TrendingUp className="h-16 w-16 text-gray-400 mx-auto mb-6" />
              <h3 className="text-xl font-medium text-gray-900 mb-4">No Trend Data Available</h3>
              <p className="text-gray-900 mb-8 max-w-md mx-auto">
                Complete multiple interviews to see your performance trends and improvement over time.
              </p>
              <Button 
                onClick={() => window.location.href = '/interview'}
                className="flex items-center mx-auto"
              >
                <Play className="mr-2 h-4 w-4" />
                Start Practicing
              </Button>
            </div>
          )}

          {activeTab === 'achievements' && (
            <div className="text-center py-12">
              <Award className="h-16 w-16 text-gray-400 mx-auto mb-6" />
              <h3 className="text-xl font-medium text-gray-900 mb-4">No Achievements Yet</h3>
              <p className="text-gray-900 mb-8 max-w-md mx-auto">
                Complete interviews and reach milestones to unlock achievements and track your progress.
              </p>
              <Button 
                onClick={() => window.location.href = '/interview'}
                className="flex items-center mx-auto"
              >
                <Play className="mr-2 h-4 w-4" />
                Start Earning Achievements
              </Button>
            </div>
          )}

          {activeTab === 'benchmarks' && (
            <div className="text-center py-12">
              <Target className="h-16 w-16 text-gray-400 mx-auto mb-6" />
              <h3 className="text-xl font-medium text-gray-900 mb-4">No Benchmark Data</h3>
              <p className="text-gray-900 mb-8 max-w-md mx-auto">
                Complete interviews to compare your performance with global averages and similar roles.
              </p>
              <Button 
                onClick={() => window.location.href = '/interview'}
                className="flex items-center mx-auto"
              >
                <Play className="mr-2 h-4 w-4" />
                Start Building Your Profile
              </Button>
            </div>
          )}
        </div>
      </div>
    </AppLayout>
  );
}